<template>
  <picture-input
    ref="pictureInput"
    width="300"
    height="400"
    margin="0"
    radius="5"
    accept="image/jpeg,image/png"
    size="10"
    button-class="btn"
    :custom-strings="{
      upload: ' <br> <br>',
      drag: ' <br> <br>(3MB  .jpg, .png)'
    }"
    @change="onChange">
  </picture-input>
</template>

<script>
import PictureInput from 'vue-picture-input'
export default {
  name: 'PhotoUpload',
  data () {
    return {
    }
  },
  components: {
    PictureInput
  },
  methods: {
    onChange (image) {
      console.log('New picture selected!')
      if (image) {
        console.log('Picture loaded.')
        this.image = image
      } else {
        console.log('FileReader API not supported: use the <form>, Luke!')
      }
    }
  }
}
</script>

<style>

</style>



