<template>
  <div>
    <Nav></Nav>
    <mdb-container>
      <mdb-card>
        <mdb-view class="px-4 py-3 white-text" gradient="aqua">
          <h2 class="card-header-title text-left"> </h2>
          <p class="mb-0 text-left">{{ `${year} ${month} ${date}` }}</p>
        </mdb-view>
        <mdb-card-body>
          <mdb-row>
              <mdb-col col="12" sm="6" lg="4"><PhotoUpload></PhotoUpload></mdb-col>
              <mdb-col col="12" sm="6" lg="8" class="text-left">
                  <section class="input">
                    <mdb-input class="mb-2 mt-0">
                      <span class="input-group-text md-addon" slot="prepend"> </span>
                      <span class="input-group-text md-addon" slot="append"></span>
                    </mdb-input>
                    <mdb-input class="mb-2 mt-0">
                      <span class="input-group-text md-addon" slot="prepend"> </span>
                      <span class="input-group-text md-addon" slot="append">%</span>
                    </mdb-input>
                    <mdb-input class="mb-2 mt-0">
                      <span class="input-group-text md-addon" slot="prepend"></span>
                      <span class="input-group-text md-addon" slot="append">%</span>
                    </mdb-input>
                    <mdb-input class="mb-2 mt-0">
                      <span class="input-group-text md-addon" slot="prepend"></span>
                      <span class="input-group-text md-addon" slot="append">%</span>
                    </mdb-input>
                    <mdb-input class="mb-2 mt-0">
                      <span class="input-group-text md-addon" slot="prepend"></span>
                      <span class="input-group-text md-addon" slot="append">kg</span>
                    </mdb-input>
                    <mdb-input class="mb-2 mt-0">
                      <span class="input-group-text md-addon" slot="prepend"></span>
                      <span class="input-group-text md-addon" slot="append">kg</span>
                    </mdb-input>
                    <mdb-input type="textarea" label="" :rows="5" />
                    <div class="text-right">
                      <mdb-btn color="default" @click="handleSaveClick"></mdb-btn>
                      <mdb-btn color="warning" @click="handleCancelClick"></mdb-btn>
                    </div>
                  </section>
              </mdb-col>
          </mdb-row>
        </mdb-card-body>
      </mdb-card>
    </mdb-container>
    <Footer></Footer>
  </div>
</template>

<script>
import Nav from '@/components/common/Nav.vue'
import Footer from '@/components/common/Footer.vue'
import PhotoUpload from '@/components/util/PhotoUpload.vue'
import {
  mdbContainer,
  mdbRow,
  mdbCol,
  mdbCard,
  mdbCardImage,
  mdbCardHeader,
  mdbCardBody,
  mdbCardTitle,
  mdbCardText,
  mdbCardFooter,
  mdbCardUp,
  mdbCardAvatar,
  mdbCardGroup,
  mdbBtn,
  mdbView,
  mdbMask,
  mdbIcon,
  mdbInput,
  mdbNumericInput
} from 'mdbvue'

export default {
  name: 'DayNew',
  components: {
    Nav,
    Footer,
    mdbContainer,
    mdbRow,
    mdbCol,
    mdbCard,
    mdbCardImage,
    mdbCardHeader,
    mdbCardBody,
    mdbCardTitle,
    mdbCardText,
    mdbCardFooter,
    mdbCardUp,
    mdbCardAvatar,
    mdbCardGroup,
    mdbBtn,
    mdbView,
    mdbMask,
    mdbIcon,
    mdbInput,
    mdbNumericInput,
    PhotoUpload
  },
  data () {
    return {
      newdate: this.$route.params.newdate,
      datearr: '',
      year: '',
      month: '',
      date: ''
    }
  },
  methods: {
    dateSplit: function () {
      this.datearr = this.newdate.split('-')
      this.year = this.datearr[0]
      this.month = this.datearr[1]
      this.date = this.datearr[2]
    },
    handleSaveClick () {
      this.$router.push({
        name: 'DayView',
        params: {
          newdate: this.newdate
        }
      })
    },
    handleCancelClick () {
      this.$router.push({
        name: 'Home'
      })
    }
  },
  beforeMount () {
    this.dateSplit()
  }
}
</script>

<style scoped>
section.preview {
  border: 1px solid #e0e0e0;
  padding: 15px;
}
</style>



