<template>
  <div>
    <Nav></Nav>
    <mdb-container>
      <mdb-card>
        <mdb-view class="px-4 py-3 white-text" gradient="aqua">
          <h2 class="card-header-title text-left"> </h2>
          <p class="mb-0 text-left">{{ `${year} ${month} ${date}` }}</p>
        </mdb-view>
        <mdb-card-body>
          <mdb-row>
              <mdb-col col="12" sm="6" lg="4">
                <img class="img-fluid rounded" alt=" " :src="photo" />
              </mdb-col>
              <mdb-col col="12" sm="6" lg="8" class="text-left" disabled>
                  <section class="input">
                    <mdb-input class="mb-2 mt-0" disabled value="{$diaryDays}">
                      <span class="input-group-text md-addon" slot="prepend"> </span>
                      <span class="input-group-text md-addon" slot="append"></span>
                    </mdb-input>
                    <mdb-input class="mb-2 mt-0" disabled value="{$diaryGoal}">
                      <span class="input-group-text md-addon" slot="prepend"> </span>
                      <span class="input-group-text md-addon" slot="append">%</span>
                    </mdb-input>
                    <mdb-input class="mb-2 mt-0" disabled value="{$diaryFat}">
                      <span class="input-group-text md-addon" slot="prepend"></span>
                      <span class="input-group-text md-addon" slot="append">%</span>
                    </mdb-input>
                    <mdb-input class="mb-2 mt-0" disabled value="{$diaryWater}">
                      <span class="input-group-text md-addon" slot="prepend"></span>
                      <span class="input-group-text md-addon" slot="append">%</span>
                    </mdb-input>
                    <mdb-input class="mb-2 mt-0" disabled value="{$diaryMuscle}">
                      <span class="input-group-text md-addon" slot="prepend"></span>
                      <span class="input-group-text md-addon" slot="append">kg</span>
                    </mdb-input>
                    <mdb-input class="mb-2 mt-0" disabled value="{$diarySkeletal}">
                      <span class="input-group-text md-addon" slot="prepend"></span>
                      <span class="input-group-text md-addon" slot="append">kg</span>
                    </mdb-input>
                    <mdb-input type="textarea" label="" value="{$diaryComment}" disabled :rows="5" />
                    <div class="text-right">
                      <mdb-btn color="default" @click="handleEditClick"></mdb-btn>
                      <mdb-btn color="danger" @click="handleDeleteClick"></mdb-btn>
                      <mdb-btn color="primary" @click="handleReturnClick"></mdb-btn>
                    </div>
                  </section>
              </mdb-col>
          </mdb-row>
        </mdb-card-body>
      </mdb-card>
    </mdb-container>
    <Footer></Footer>
  </div>
</template>

<script>
import Nav from '@/components/common/Nav.vue'
import Footer from '@/components/common/Footer.vue'
import axios from 'axios'
import PhotoUpload from '@/components/util/PhotoUpload.vue'
import {
  mdbContainer,
  mdbRow,
  mdbCol,
  mdbCard,
  mdbCardImage,
  mdbCardHeader,
  mdbCardBody,
  mdbCardTitle,
  mdbCardText,
  mdbCardFooter,
  mdbCardUp,
  mdbCardAvatar,
  mdbCardGroup,
  mdbBtn,
  mdbView,
  mdbMask,
  mdbIcon,
  mdbInput,
  mdbNumericInput
} from 'mdbvue'

export default {
  name: 'DayView',
  components: {
    Nav,
    Footer,
    mdbContainer,
    mdbRow,
    mdbCol,
    mdbCard,
    mdbCardImage,
    mdbCardHeader,
    mdbCardBody,
    mdbCardTitle,
    mdbCardText,
    mdbCardFooter,
    mdbCardUp,
    mdbCardAvatar,
    mdbCardGroup,
    mdbBtn,
    mdbView,
    mdbMask,
    mdbIcon,
    mdbInput,
    mdbNumericInput,
    PhotoUpload
  },
  data () {
    return {
      context: 'http://localhost:8080/diary',
      newdate: this.$route.params.newdate,
      datearr: '',
      year: '',
      month: '',
      date: '',
      diaryDate: '',
      diaryPhoto: '',
      diaryDays: '',
      diaryGoal: '',
      diaryFat: '',
      diaryWater: '',
      diaryMuscle: '',
      diarySkeletal: '',
      diaryComment: '',
      photo: require('@/assets/diary_01.jpg')
    }
  },
  methods: {
    dateSplit () {
      this.datearr = this.newdate.split('-')
      this.year = this.datearr[0]
      this.month = this.datearr[1]
      this.date = this.datearr[2]
    },
    read () {
      alert(`${this.context}/find/${this.newdate}`)
      axios.get(`${this.context}/find/${this.newdate}`)
        .then(res => {
          alert(`SUCCESS: ${res.data.diaryDays}`)
          this.diaryDate = res.data.diaryDate
          this.diaryPhoto = res.data.diaryPhoto
          this.diaryDays = res.data.diaryDays
          this.diaryGoal = res.data.diaryGoal
          this.diaryFat = res.data.diaryFat
          this.diaryWater = res.data.diaryWater
          this.diaryMuscle = res.data.diaryMuscle
          this.diarySkeletal = res.data.diarySkeletal
          this.diaryComment = res.data.diaryComment
        })
        .catch(e => {
          alert('ERROR')
        })
    },
    handleEditClick () {
      this.$router.push({
        name: 'DayEdit',
        params: {
          newdate: this.newdate
        }
      })
    },
    handleDeleteClick () {
      this.$router.push({
        name: 'Home'
      })
    },
    handleReturnClick () {
      this.$router.push({
        name: 'Home'
      })
    }
  },
  beforeMount () {
    this.dateSplit()
    this.read()
  }
}
</script>

<style scoped>
section.preview {
  border: 1px solid #e0e0e0;
  padding: 15px;
}
</style>



